@import '../../appScss/colors.scss';
@import '../../appScss/mixins.scss';

.card {
  max-width: 272px;
  border: 1px solid var(--elements-color);
  // -webkit-box-shadow: 0px 3px 11px 0px rgba(0, 0, 0, 0.75);
  // -moz-box-shadow: 0px 3px 11px 0px rgba(0, 0, 0, 0.75);
  // box-shadow: 0px 3px 11px 0px rgba(0, 0, 0, 0.75);

  @include onTablet {
    max-width: 592px;
  }

  @include onDesktop {
    max-width: 752px;
  }

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.content {
  width: 272px;
  height: 128px;
  padding: 24px;

  @include onTablet {
    width: 592px;
  }

  @include onDesktop {
    width: 752px;
  }
}

.close {
  width: 16px;
  height: 16px;
}

.image {
  width: 80px;
  height: 80px;
}

.name {
  width: 336px;
  height: 21px;
}

.buttons {
  width: 96px;
  height: 32px;
}

.price {
  width: 80px;
  height: 32px;
}
